import React from "react";
import icon__site from "../images/icon__site.svg";
import icon__callcenter from "../images/icon__callcenter.svg";
import icon__dogovor from "../images/icon__dogovor.svg";
import icon__package from "../images/icon__package.svg";
import icon__delivery from "../images/icon__delivery.svg";

function Oplata() {
  return (
    <section className="payment" id="srok">
      <p class="models__subtitle"> </p>
      <h2 className="calculate__header">    ?</h2>
      <ul className="payment__steps">
        <li className="payment__step">
          <img className="payment__icon" src={icon__site} alt=" " />
          <h3 className="payment__title">1.  </h3>
          <p className="payment__text">   ,      </p>
        </li>
        <li className="payment__step">
          <img className="payment__icon" src={icon__callcenter} alt=" " />
          <h3 className="payment__title">2.    </h3>
          <p className="payment__text">      .       .</p>
        </li>
        <li className="payment__step">
          <img className="payment__icon" src={icon__dogovor} alt=" " />
          <h3 className="payment__title">3.  </h3>
          <p className="payment__text">      50%,  ,   .</p>
        </li>
        <li className="payment__step">
          <img className="payment__icon" src={icon__package} alt=" " />
          <h3 className="payment__title">4.    </h3>
          <p className="payment__text">   50%    .</p>
        </li>
        <li className="payment__step">
          <img className="payment__icon" src={icon__delivery} alt=" " />
          <h3 className="payment__title">5.   </h3>
          <p className="payment__text">   100%        .</p>
        </li>
      </ul>
    </section>
  );
  }

  export default Oplata;